<template>
	<view class="main" :class="{'main1':style=='周杰伦','main2':style!='周杰伦'}">
		<view style="height: 5vh;"></view>
		<view class="style" :class="{'style1':style=='周杰伦','style2':style!='周杰伦'}">
			<view class="img">
				<image :src="imgsrc"></image>
			</view>
			<view class="text">
				<view class="text1">{{style}}</view>
				<view class="text2">{{detail}}</view>
			</view>
		</view>
		<scroll-view scroll-y="true">
			<view class="lyrics">
				<view class="lyric" v-for="item in lyrics">
					{{item}}
				</view>
			</view>
		</scroll-view>
	</view>
</template>

<script>
	export default{
		data(){
			return{
				imgsrc:"../static/images/singer/zhoujielun1.jpg",
				style:"周杰伦",
				detail:"晴天",
				lyrics:[
					"故事的小黄花","从出生那年就飘着",
					"童年的荡秋千","随记忆一直晃到现在",
					"ReSoSoSiDoSiLa","SoLaSiSiSiSiLaSiLaSo",
					"吹着前奏望着天空","我想起花瓣试着掉落",
					"为你翘课的那一天","花落的那一天",
					"教室的那一间","我怎么看不见",
					"消失的下雨天","我好想再淋一遍",
					"没想到失去的勇气我还留着","好想再问一遍",
					"你会等待还是离开","刮风这天我试过握着你手",
					"但偏偏雨渐渐","大到我看你不见",
					"还要多久我才能在你身边","等到放晴的那天",
					"也许我会比较好一点","从前从前有个人爱你很久",
					"但偏偏风渐渐","把距离吹得好远",
					"好不容易又能再多爱一天","但故事的最后",
					"你好像还是说了拜拜",
				]
			}
		},
		onLoad(data) {
			// console.log(data)
			this.style = data.name;			
			this.imgsrc = "../static/images/singer/" + data.style + ".jpg";
			this.lyrics = this.$store.state.lyrics;
		}
	}
</script>

<style>
	.main{
		width: 100vw;
		height: 100vh;
	}
	
	.main1{
		background-color: #a56f7a;
	}
	
	.main2{
		background-color: #6FA1A5;
	}
	
	.style{
		width: 80vw;
		height: 25vh;
		margin-left: 50px;
		margin: 0 auto;
		display: flex;
		flex-direction: row;
		align-items: center;
		border-radius: 20px;
	}
	
	.style1{
		box-shadow:  24px 24px 41px #6d4951,
		             -24px -24px 41px #dd95a3;
	}
	
	.style2{
		box-shadow:  24px 24px 41px #466568,
		             -24px -24px 41px #98dde2;
	}
	
	.img image{
		width: 35vw;
		height: 35vw;
		margin-left: 15px;
		border-radius: 20px;
	}
	
	.text{
		margin-left: 50px;
	}
	
	.text1{
		margin-bottom: 30px;
		font-size: 30px;
		font-family: "楷体","楷体_GB2312";
		color: #3a524e;
	}
	
	.text2{
		color: #53556c;
		font-size: 20px;
	}
	
	scroll-view{
		height: 65vh;
		margin: 20px;
	}
	
	.lyrics{
		margin-left: 25px;
	}
	
	.lyric{
		margin-top: 5px;
	}
</style>
